@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;700&display=swap");
body {
  font-family: Roboto, sans-serif;
  font-style: normal;
}

.container {
  width: 1440px;
  background: #FFFFFF;
  display: flex;
  margin: auto;
  margin-top: 5%;
  width: 50%;
}

.content > * {
  margin-top: 8%;
}

.mainImage {
  margin-right: 5%;
  object-fit: cover;
}

.h1 {
  font-weight: 700;
  font-size: 42px;
  line-height: 120%;
  color: #121212;
}

.p {
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  color: #121212;
}

@media (max-width: 1000px) {
  .container {
    display: block;
    width: 90%;
  }

  .mainImage {
    float: none;
    width: 100%;
  }
}
.btn {
  padding: 16px 40px;
  background: #E4C1AE;
  border-style: solid;
  border-color: #E4C1AE;
  font-weight: 700;
  font-size: 16px;
  line-height: 100%;
}
.btnOpenPopup {
  color: #FFFFFF;
  border-radius: 32px;
}
.btnReset {
  color: black;
  border-color: black;
}
.btn:hover {
  cursor: pointer;
  background: #e49469;
}

.modal {
  display: none;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.5);
}

.modal-content {
  background-color: #fefefe;
  margin: 15% auto;
  padding: 20px;
  border: 1px solid #888;
  width: 60%;
}
.modal-content > * {
  margin-left: 5%;
}

.close {
  color: black;
  float: right;
  font-size: 28px;
  font-weight: bold;
}
.close:hover {
  cursor: pointer;
}